body {
  font-family: Arial, sans-serif;
  background-color: #E6F3FF;
  color: #333;
  margin: 0;
  padding: 0;
  background-image: url('./assets/background.png');
  background-size: cover;
  background-attachment: fixed;
  background-opacity: 0.1;
}

.container {
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.logo {
  display: block;
  margin: 0 auto;
  width: 150px;
}

h1 {
  color: #4A90E2;
  text-align: center;
  font-size: 2.5em;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
}

.form-group {
  margin-bottom: 15px;
}

label {
  display: block;
  margin-bottom: 5px;
  color: #4A90E2;
}

input, select {
  width: 100%;
  padding: 10px;
  border: 1px solid #4A90E2;
  border-radius: 5px;
  background-color: #FFFFFF;
}

button {
  background-color: #4A90E2;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.3s;
  display: block;
  margin: 20px auto;
}

button:hover {
  background-color: #357ABD;
}

.result {
  margin-top: 20px;
  padding: 15px;
  border-radius: 10px;
}

.success {
  background-color: #DFF0D8;
  color: #3C763D;
}

.error {
  background-color: #F2DEDE;
  color: #A94442;
}

.footer {
  text-align: center;
  margin-top: 50px;
  color: #4A90E2;
  font-size: 0.9em;
}

@media (max-width: 768px) {
  .container {
    margin: 10px;
    padding: 15px;
  }

  h1 {
    font-size: 2em;
  }
}